import { useState } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import { Link } from 'react-router-dom';

function ControlledCarousel() {
    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex) => {
        setIndex(selectedIndex);
    };

    return (
        <Carousel activeIndex={index} onSelect={handleSelect}>
            <Carousel.Item>
                <Link to="/detail/14">
                    <img
                        className="d-block w-100"
                        src="/img/slide_1.jpg"
                        alt="First slide"
                    />
                    <Carousel.Caption>
                        <h6>TIPICOSI</h6>
                        <h2>SUMMER<br />LAUNCHING</h2>
                        <p># # #</p>
                    </Carousel.Caption>
                </Link>
            </Carousel.Item>

            <Carousel.Item>
                <Link to="/detail/108">
                    <img
                        className="d-block w-100"
                        src="/img/slide_2.jpg"
                        alt="Second slide"
                    />
                    <Carousel.Caption>
                        <h6>GOLF</h6>
                        <h2>CASUAL<br />OR CLASSY</h2>
                        <p>6  </p>
                    </Carousel.Caption>
                </Link>
            </Carousel.Item>

            <Carousel.Item>
                <Link to="/detail/98">
                    <img
                        className="d-block w-100"
                        src="/img/slide_3.jpg"
                        alt="Third slide"
                    />
                    <Carousel.Caption>
                        <h6>LUXURY</h6>
                        <h2>  <br />LF </h2>
                        <p>  </p>
                    </Carousel.Caption>
                </Link>
            </Carousel.Item>

            <Carousel.Item>
                <Link to="/detail/54">
                    <img
                        className="d-block w-100"
                        src="/img/slide_4.jpg"
                        alt="Second slide"
                    />

                    <Carousel.Caption>
                        <h6>KIDS</h6>
                        <h2><br /> !</h2>
                        <p>    </p>
                    </Carousel.Caption>
                </Link>
            </Carousel.Item>

            <Carousel.Item>
                <Link to="/detail/8">
                    <img
                        className="d-block w-100"
                        src="/img/slide_5.jpg"
                        alt="Second slide"
                    />

                    <Carousel.Caption>
                        <h6>  </h6>
                        <h2><br /></h2>
                        <p>    </p>
                    </Carousel.Caption>
                </Link>
            </Carousel.Item>
        </Carousel>
    );
}

export default ControlledCarousel;